.arrow-up {
  height: 40px;
  width: 200px;
  border: 2px solid #F7CA17;
  border-radius: .5rem;
  position: relative;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.arrow-slide {
  left: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  background: #F7CA17;
  position: absolute;
  display: block;
  z-index: 0;
}
.left-arm {
  position: absolute;
  z-index: 1;
  background-color: transparent;
  top: 19px;
  left: 3px;
  width: 20px;
  display: block;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  margin-left: 15px;
}
.left-arm:after {
  content: "";
  background-color: #F7CA17;
  width: 20px;
  height: 1px;
  display: block;
  border-radius: 1px;
  transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
  -webkit-transform-origin: right center;
          transform-origin: right center;
  z-index: -1;
}
.right-arm {
  position: absolute;
  z-index: 1;
  background-color: transparent;
  top: 19px;
  left: 17px;
  width: 20px;
  display: block;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  border-radius: 2px;
  margin-left: 15px;
}
.right-arm:after {
  content: "";
  background-color: #F7CA17;
  width: 20px;
  height: 1px;
  display: block;
  border-radius: 1px;
  transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
  -webkit-transform-origin: left center;
          transform-origin: left center;
  z-index: -1;
}
.arrow-up:hover {
  transition: all 0.1s;
}
.arrow-up:hover .left-arm:after {
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg);
}
.arrow-up:hover .right-arm:after {
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
}
.arrow-up:hover .arrow-slide {
  transition: all 0.4s ease-in-out;
  -webkit-transform: translateY(200%);
          transform: translateY(200%);
}

