.tabs {
  border-radius: 10px;
  overflow: hidden;
  box-shadow:   0 7px 30px -10px rgb(0, 0, 0); 
  width: 63%;
}
.tab {
  width: 100%;
  color: white;
  overflow: hidden;
}
.tab-label {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 30px;
  background: #88858546;
  font-weight: bold;
  cursor: pointer;
  font-size: 17px;
}
.tab-label:hover {
  background: #1b1b1b;
}
.tab-label::after {
  content: "\276F";
  width: 1em;
  height: 1em;
  text-align: center;
  transition: all 0.35s;
}
.tab-content {
  max-height: 0;
  padding: 0 1em;
  color: #2c3e50;
  background: white;
  transition: all 0.35s;
}
.tab-close {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 1em;
  font-size: 0.75em;
  background: #2c3e50;
  cursor: pointer;
}
.tab-close:hover {
  background: #1a252f;
}
input:checked + .tab-label::after {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
input:checked ~ .tab-content {
  max-height: 100vh;
  padding: 1em;
}



.sponsor{
    display: flexbox;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom: -100px;
}
.slide-container{
    background: #ffffff;
    width: 65%;
    border-radius: 10px;
    margin-bottom: 30px;
}
.slider-1, .slider-2, .slider-3, .slider-4 {
    background: #ffffff;
    height: 100px;
    overflow: hidden;
    position: relative;
    margin-top: 30px;
    margin-bottom: 30px;
}


.slider-1 .slide-track {
    -webkit-animation: scroll 36s linear infinite;
            animation: scroll 36s linear infinite;
    display: -webkit-flex;
    display: flex;
}

.slider-2 .slide-track {
    -webkit-animation: scroll 31s linear infinite;
            animation: scroll 31s linear infinite;
    display: -webkit-flex;
    display: flex;
}

.slider-3 .slide-track {
    -webkit-animation: scroll 33s linear infinite;
            animation: scroll 33s linear infinite;
    display: -webkit-flex;
    display: flex;
}

.slider-4 .slide-track {
    -webkit-animation: scroll 39s linear infinite;
            animation: scroll 39s linear infinite;
    display: -webkit-flex;
    display: flex;
}

.slide{
    padding-left: 70px;
}


@-webkit-keyframes scroll {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
   }
    100% {
        -webkit-transform: translateX(calc(-200px * 4));
                transform: translateX(calc(-200px * 4));
   }
}


@keyframes scroll {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
   }
    100% {
        -webkit-transform: translateX(calc(-200px * 4));
                transform: translateX(calc(-200px * 4));
   }
}
