.profileName {
    text-align: center;
    font: normal normal 600 17px/28px "Montserrat";
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
  }
  .about-profile-pos {
    font-size: 13px;
    font-family: "Montserrat";
    font-weight: 400;
    line-height: 20px;
    color: #ffffff;
  }
  
  #mobileAboutCol {
    margin: 0 50px 20px 50px;
  }
  
  .about-h1 {
    font: normal normal bold 45px/58px "Montserrat";
    letter-spacing: 3px;
    /* text-transform: uppercase; */
    /* background-color: #d7b5ff; */
    color: #000000;
  }
  
  .about-h3 {
    font: normal normal bold 25px/43px "Montserrat";
    font-weight: 700;
    letter-spacing: 0px;
    color: #000000;
  }
  .about-reg1 {
    font: normal normal 600 18px/24px "Montserrat";
    letter-spacing: 0px;
    color: #512783;
  }
  
  .about-profile-img {
    width: 210px;
    height: 220px;
  }
  
  .ourTeam {
    font: italic normal bold 35px/45px Montserrat;
    margin-left: 60px;
  }
  
  .aboutPicture {
    height: 500px;
    width: auto;
  }
  
  .teamPic {
    height: 275px;
    width: auto;
    margin-top: 20px;
  }
  
  .statCard {
    margin: 40px;
    width: 450px;
    margin-bottom: -100px;
  }
  
  .numParticipants {
    display: block;
    margin: 0 auto;
    width: 650px;
    height: auto;
    margin-top: -80px;
  }
 
  .mission {
      font-size: 25px;
  }
  .mission-info{
    font-size: 20px;
    margin-top: -10px;
  }
  .container-1{
    width: 100%;
  }
  
  @media only screen and (max-width: 1300px) {
    .aboutPicture {
      height: 370px;
    }
  }
  
  @media only screen and (max-width: 1000px) {
    .statCard {
      width: 350px;
      margin: 15px;
    }
    .ourTeam {
      text-align: center;
    }
    
    .numParticipants {
      width: 500px;
    }
  }
  
  @media only screen and (max-width: 1160px) {
    .statCard {
      width: 400px;
      margin: 20px;
    }
    .aboutPicture {
      height: 350px;
    }
  }
  
  @media only screen and (max-width: 860px) {
    .statCard {
      height: 550px;
      width: auto;
      margin: 20px;
    }
  }
  
  @media only screen and (max-width: 700px) {
    .aboutPicture {
      width: 350px;
      height: auto;
    }
    .teamPic {
      width: 350px;
      height: auto;
    }
  
    .numParticipants {
      width: 400px;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .container-1 {
      padding: 5px;
      padding-top: 250px;
      margin-top: -300px;
    }
  
    .statCard {
      height: 400px;
      margin: 5px;
    }
  }
  
  @media only screen and (max-width: 500px) {
    .aboutPicture {
      width: 260px;
      height: auto;
    }
    .teamPic {
      width: 260px;
      height: auto;
    }
    #mobileAboutCol {
      margin: 20px 0;
    }
  
    .numParticipants {
      width: 325px;
    }
  }
  
  .waves-3{
    background: url(/src/Components/Hero/Hero-imgs/waves.svg);
    background-size: 100% 100%;
    margin-top: 250px;
    width: 100%;
    height: 70%;
    position: absolute;
    z-index: -1;
    opacity: 0.2;
  }
  